.about_content {
	height: 90%;
	top: 5%;
	left: 55px;
	min-height: 566px;
	display: -ms-flexbox;
	display: flex;
	position: absolute;
	-ms-flex-direction: column;
	flex-direction: column;
	-ms-flex-pack: justify;
	justify-content: space-between;
	width: calc(100% - 55px);
}

.about_content .text_h1 {
	color: #08fdd8;
}

.about_main {
	margin-left: 6%;
	width: 35%;
	vertical-align: middle;
	display: table-cell;
	max-height: 90%;
}

.about_main p {
	font-size: 14px;
	line-height: 18px;
	color: #ffffff;
	font-family: "Open Sans", sans-serif;
}

.player {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	width: 40%;
	height: 100%;
	position: absolute;
	right: 0;
	overflow: hidden;
}

.player iframe {
	height: 115px;
	width: 400px;
	position: absolute;
	bottom: 0;
	right: 0;

}



